.loadMoreBtn {
  display: block;
  margin: auto;
  min-width: 180px;
  padding: 8px 16px;
  background-color: #3f51b5;
  color: #fff;
  text-align: center;
  text-decoration: none;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  border: 0;
  border-radius: 2px;
  outline: 0;
  cursor: pointer;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.loadMoreBtn:hover,
.loadMoreBtn:focus {
  background-color: #303f9f;
}